
.footer {
    padding: 27px 16px 37px;
    text-align: center;
}

.footer--logo {
    margin-bottom: 20px;
}

.footer--contact {
    display: flex;
    justify-content: center;
    border-top: solid 1px #E5E5E5;
    border-bottom: solid 1px #E5E5E5;
}

.footer--item {
    display: flex;
    align-items: center;

    span {
        font-size: 16px;
        color: #4A4A4A;
    }
}

.footer--legal {
    padding: 16px 0 11px;
    font-size: 13px;

    span {
        margin: 0 10px;
    }

    a {
        color: #4A4A4A;
    }
}

.footer--rights {
    padding: 16px 0;
    font-size: 13px;
    color: #4A4A4A;
}

.footer--rrss {
    padding: 5px;
    border-top: solid 1px #E5E5E5;

    img {
        margin: 5px;
    }
}

@media only screen and (min-width: 950px) {

    .footer {
        position: relative;
        width: 100%;
        margin: auto;
        box-sizing: border-box;
    }

    .footer--info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: solid 1px #E5E5E5;
    }

    .footer--legal {
        position: absolute;
        right: 0;
        bottom: 50px;
        left: 0;
        width: 250px;
        margin: auto;
    }

    .footer--contact {
        justify-content: flex-end;
        padding: 20px;
    }

    .footer--logo {
        position: absolute;
        top: 55px;
        left: 30px;
    }

    .footer--rights {
        padding: 16px 0 16px 16px;
    }
}
